// Êúà‰ªΩÂØºËà™Áõ∏ÂÖ≥ÊñπÊ≥ï
const previousMonth = () => {
  if (currentMonth.value === 1) {
    currentMonth.value = 12
    currentYear.value -= 1
  } else {
    currentMonth.value -= 1
  }
}

const nextMonth = () => {
  if (currentMonth.value === 12) {
    currentMonth.value = 1
    currentYear.value += 1
  } else {
    currentMonth.value += 1
  }
}

const showMonthYearPicker = () => {
  // ÂàáÊç¢Âà∞Êúà‰ªΩÈÄâÊã©Ê®°Âºè
  selectionMode.value = 'month'
