  selectedYear.value = currentYear.value
  selectedMonth.value = currentMonth.value
}

// ÊªëÂä®Â§ÑÁêÜÊñπÊ≥ï
const handleTouchStart = (e) => {
  if (!isSwipeEnabled.value) return
  const touch = e.touches[0]
  touchStartY.value = touch.clientY
  touchStartX.value = touch.clientX
}

const handleTouchMove = (e) => {
  // ‰∏çÂ§ÑÁêÜËß¶Êë∏ÁßªÂä®‰∫ã‰ª∂ÔºåÈÅøÂÖçÊÄßËÉΩË≠¶Âëä
  // ÊªëÂä®Ê£ÄÊµãÂú® touchend ‰∫ã‰ª∂‰∏≠Â§ÑÁêÜ
}

const handleTouchEnd = (e) => {
  if (!isSwipeEnabled.value) return
  const touch = e.changedTouches[0]
  const deltaY = touch.clientY - touchStartY.value
  const deltaX = touch.clientX - touchStartX.value
  
  // Âà§Êñ≠ÊòØÂê¶‰∏∫ÂûÇÁõ¥ÊªëÂä®Ôºà‰∏ä‰∏ãÊªëÂä®Ôºâ
  if (Math.abs(deltaY) > Math.abs(deltaX) && Math.abs(deltaY) > 50) {
    if (deltaY > 0) {
      // Âêë‰∏ãÊªëÂä®ÔºåÊòæÁ§∫‰∏ä‰∏Ä‰∏™Êúà
      previousMonth()
    } else {
      // Âêë‰∏äÊªëÂä®ÔºåÊòæÁ§∫‰∏ã‰∏Ä‰∏™Êúà
      nextMonth()
    }
  }
}

// Ëé∑ÂèñËÆ¢ÂçïÂàóË°®
const getOrders = async (isRefresh = false) => {
  try {
    loading.value = true
    const token = getToken()
    const account = getAccount()
    
    if (!token) {
      uni.showToast({
        title: 'ËØ∑ÂÖàÁôªÂΩï',
        icon: 'none'
      })
      setTimeout(() => {
        uni.navigateTo({ url: '/pages/login/login' })
      }, 1500)
      return
    }
    
    if (!account) {
      // Â¶ÇÊûúaccount‰∏∫Á©∫ÔºåÂ∞ùËØï‰ΩøÁî®token‰Ωú‰∏∫accountÔºà‰∏¥Êó∂Ëß£ÂÜ≥ÊñπÊ°àÔºâ
      const tokenAccount = token || 'default_user'
      
      // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞
      const queryParams = {
        account: tokenAccount,
        page: currentPage.value.toString(),
        limit: pageSize.value.toString()
      }
      
      // Â¶ÇÊûúÊúâÊêúÁ¥¢ÂÖ≥ÈîÆËØçÔºåÊ∑ªÂä†ËÆ¢ÂçïÂè∑Êü•ËØ¢
      if (searchKeyword.value.trim()) {
        queryParams.orderNo = searchKeyword.value.trim()
      }
      
      // Ê∑ªÂä†Êó•ÊúüËåÉÂõ¥Êü•ËØ¢ÂèÇÊï∞
      if (startDate.value && endDate.value) {
        // Êó•ÊúüËåÉÂõ¥Ê®°Âºè
        queryParams.startDate = formatDateForAPI(startDate.value)
        queryParams.endDate = formatDateForAPI(endDate.value)
      } else {
        // Êúà‰ªΩÊ®°Âºè
        queryParams.year = currentYear.value.toString()
        queryParams.month = currentMonth.value.toString()
      }
      
      const response = await apiOrderQuery(queryParams)
      
      if (response.code === 200) {
        // ‰øÆÂ§çÊï∞ÊçÆÁªìÊûÑÔºö‰ªé response.data.records Ëé∑ÂèñËÆ¢ÂçïÂàóË°®
        const newOrders = response.data?.records || []
        
        if (isRefresh) {
          // Âà∑Êñ∞Êó∂ÊõøÊç¢Êï∞ÊçÆ
          orders.value = newOrders
          currentPage.value = 1
        } else {
          // Âä†ËΩΩÊõ¥Â§öÊó∂ËøΩÂä†Êï∞ÊçÆ
          if (currentPage.value === 1) {
            orders.value = newOrders
          } else {
            orders.value = [...orders.value, ...newOrders]
          }
        }
        
        // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ - ‰ΩøÁî®Êé•Âè£ËøîÂõûÁöÑÂàÜÈ°µ‰ø°ÊÅØ
        const totalPages = response.data?.pages || 1
        hasMore.value = currentPage.value < totalPages
        
        // ‰øùÂ≠òÊÄªËÆ¢ÂçïÊï∞
        totalOrders.value = response.data?.total || 0
      } else {
        uni.showToast({
          title: response.msg || 'Ëé∑ÂèñËÆ¢ÂçïÂ§±Ë¥•',
          icon: 'none'
        })
      }
      
      loading.value = false
      return
    }
    
    // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞
    const queryParams = {
      account: account,
      page: currentPage.value.toString(),
      limit: pageSize.value.toString()
    }
    
    // Â¶ÇÊûúÊúâÊêúÁ¥¢ÂÖ≥ÈîÆËØçÔºåÊ∑ªÂä†ËÆ¢ÂçïÂè∑Êü•ËØ¢
    if (searchKeyword.value.trim()) {
      queryParams.orderNo = searchKeyword.value.trim()
    }
    
    // Ëé∑ÂèñÊâÄÊúâÊï∞ÊçÆÂêéÂú®ÂâçÁ´ØËøáÊª§ÔºåÁ°Æ‰øùÊï∞ÊçÆÂÆåÊï¥ÊÄß
    const response = await apiOrderQuery(queryParams)
    
    if (response.code === 200) {
      // ‰øÆÂ§çÊï∞ÊçÆÁªìÊûÑÔºö‰ªé response.data.records Ëé∑ÂèñËÆ¢ÂçïÂàóË°®
      const newOrders = response.data?.records || []
      
      if (isRefresh) {
        // Âà∑Êñ∞Êó∂ÊõøÊç¢Êï∞ÊçÆ
        orders.value = newOrders
        currentPage.value = 1
      } else {
        // Âä†ËΩΩÊõ¥Â§öÊó∂ËøΩÂä†Êï∞ÊçÆ
        if (currentPage.value === 1) {
          orders.value = newOrders
        } else {
          orders.value = [...orders.value, ...newOrders]
        }
      }
      
      // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ - ‰ΩøÁî®Êé•Âè£ËøîÂõûÁöÑÂàÜÈ°µ‰ø°ÊÅØ
      const totalPages = response.data?.pages || 1
      hasMore.value = currentPage.value < totalPages
      
        // ‰øùÂ≠òÊÄªËÆ¢ÂçïÊï∞
        totalOrders.value = response.data?.total || 0
    } else {
      uni.showToast({
        title: response.msg || 'Ëé∑ÂèñËÆ¢ÂçïÂ§±Ë¥•',
        icon: 'none'
      })
    }
  } catch (error) {
    uni.showToast({
      title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑ÈáçËØï',
      icon: 'none'
    })
  } finally {
    loading.value = false
  }
}

// Ê†ºÂºèÂåñÊó∂Èó¥
const formatTime = (timeStr) => {
  if (!timeStr) return ''
  const date = new Date(timeStr)
  return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`
}

// Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊñáÊú¨
const getStatusText = (status) => {
  const statusMap = {
    0: 'ÂæÖÊîØ‰ªò',
    1: 'Â∑≤ÊîØ‰ªò',
    2: 'Â∑≤ÂèñÊ∂à',
    3: 'Â∑≤ÂÆåÊàê'
  }
  return statusMap[status] || 'Êú™Áü•Áä∂ÊÄÅ'
}

// Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊ†∑ÂºèÁ±ª
const getStatusClass = (status) => {
  const classMap = {
    0: 'status-pending',
    1: 'status-paid',
    2: 'status-cancelled',
    3: 'status-completed'
  }
  return classMap[status] || 'status-unknown'
}

// Êü•ÁúãËÆ¢ÂçïËØ¶ÊÉÖ
const viewOrderDetail = (order) => {
  uni.showModal({
    title: 'ËÆ¢ÂçïËØ¶ÊÉÖ',
    content: `ËÆ¢ÂçïÂè∑Ôºö${order.orderNo}\nÈáëÈ¢ùÔºö¬•${order.amount}\nÁä∂ÊÄÅÔºö${getStatusText(order.status)}\nÊó∂Èó¥Ôºö${formatTime(order.updateTime || order.createTime)}`,
    showCancel: false
  })
}


// ÊêúÁ¥¢Áõ∏ÂÖ≥ÊñπÊ≥ï
const focusSearch = () => {
  isSearching.value = true
}

const handleSearchBlur = () => {
  // Âª∂ËøüÂ§±ÁÑ¶ÔºåËÆ©Áî®Êà∑ÊúâÊó∂Èó¥ÁÇπÂáªÊ∏ÖÈô§ÊåâÈíÆ
  setTimeout(() => {
    if (!searchKeyword.value.trim()) {
      isSearching.value = false
    }
  }, 200)
}

const handleSearchInput = () => {
  // ÊêúÁ¥¢ËæìÂÖ•Êó∂ÁöÑÂ§ÑÁêÜ
  console.log('ÊêúÁ¥¢ÂÖ≥ÈîÆËØç:', searchKeyword.value)
  
  // Â¶ÇÊûúËæìÂÖ•Ê°Ü‰∏∫Á©∫ÔºåËá™Âä®Ëé∑ÂèñÊâÄÊúâËÆ¢Âçï
  if (!searchKeyword.value.trim()) {
    currentPage.value = 1
    getOrders(true)
  }
}

const clearSearch = () => {
  searchKeyword.value = ''
  isSearching.value = false
  // Ê∏ÖÁ©∫ÊêúÁ¥¢ÂêéÈáçÊñ∞Ëé∑ÂèñÊâÄÊúâËÆ¢Âçï
  currentPage.value = 1
  getOrders(true)
}

// ÊâßË°åÊêúÁ¥¢
const performSearch = async () => {
  if (!searchKeyword.value.trim()) {
    // Â¶ÇÊûúÊêúÁ¥¢Ê°Ü‰∏∫Á©∫ÔºåËé∑ÂèñÊâÄÊúâËÆ¢Âçï
    uni.showToast({
      title: 'ËØ∑ËæìÂÖ•ËÆ¢ÂçïÂè∑',
      icon: 'none'
    })
    return
  }
  
  // Ê∑ªÂä†Âà∞ÊêúÁ¥¢ÂéÜÂè≤
  addToSearchHistory(searchKeyword.value.trim())
  
  // ÊâßË°åÊêúÁ¥¢
  console.log('ÊâßË°åËÆ¢ÂçïÊêúÁ¥¢:', searchKeyword.value.trim())
  currentPage.value = 1
  await getOrders(true)
}

// Ê∑ªÂä†Âà∞ÊêúÁ¥¢ÂéÜÂè≤
const addToSearchHistory = (keyword) => {
  if (!keyword || keyword.length < 3) return
  
  // ÁßªÈô§ÈáçÂ§çÈ°π
  const index = searchHistory.value.indexOf(keyword)
  if (index > -1) {
    searchHistory.value.splice(index, 1)
  }
  
  // Ê∑ªÂä†Âà∞ÂºÄÂ§¥
  searchHistory.value.unshift(keyword)
  
  // ÈôêÂà∂ÂéÜÂè≤ËÆ∞ÂΩïÊï∞Èáè
  if (searchHistory.value.length > 5) {
    searchHistory.value = searchHistory.value.slice(0, 5)
  }
  
  // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
  uni.setStorageSync('orderSearchHistory', searchHistory.value)
}

// ‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩÊêúÁ¥¢ÂéÜÂè≤
const loadSearchHistory = () => {
  try {
    const history = uni.getStorageSync('orderSearchHistory')
    if (history && Array.isArray(history)) {
      searchHistory.value = history
    }
  } catch (error) {
    console.error('Âä†ËΩΩÊêúÁ¥¢ÂéÜÂè≤Â§±Ë¥•:', error)
  }
}

// ÈÄâÊã©ÊêúÁ¥¢Âª∫ËÆÆ
const selectSuggestion = (keyword) => {
  searchKeyword.value = keyword
  performSearch()
}

// Âä†ËΩΩÊõ¥Â§öËÆ¢Âçï
const loadMoreOrders = async () => {
  if (!hasMore.value || loading.value) return
  
  currentPage.value++
  await getOrders(false)
}

// È°µÈù¢Âä†ËΩΩÊó∂Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÂπ∂Ëé∑ÂèñËÆ¢Âçï
onMounted(async () => {
  const token = getToken()
  const account = getAccount()
  
  if (!token) {
    uni.showToast({
      title: 'ËØ∑ÂÖàÁôªÂΩï',
      icon: 'none'
    })
    setTimeout(() => {
      uni.navigateTo({ url: '/pages/login/login' })
    }, 1500)
  } else if (!account) {
    uni.showToast({
      title: 'Áî®Êà∑‰ø°ÊÅØÂºÇÂ∏∏ÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï',
      icon: 'none'
    })
    setTimeout(() => {
      uni.navigateTo({ url: '/pages/login/login' })
    }, 1500)
  } else {
    // Âä†ËΩΩÊêúÁ¥¢ÂéÜÂè≤
    loadSearchHistory()
    // Ëé∑ÂèñËÆ¢ÂçïÂàóË°®
    await getOrders()
  }
})
</script>

